h2 {
    text-align: center;
    font-family: 'Roboto', sans-serif;
}
.about-container {
    display: grid;
    font-family: 'Roboto', sans-serif;
}
.single-div {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-column: span 1 / span 1;
    margin-top: 1rem;
    padding: 1rem;
    justify-content: center;
}
.about-img {
    height: 200px;
}